import React from 'react';
import { motion } from "framer-motion";
import logo from '../assets/images/logo.svg';
import Blink from 'react-blink-text';


     
const Header = (props) => (
    <header id="header" className="alt">
        <motion.span className="logo" 
        whileHover={{
          scale:1.5, 
          textShadow : "0 10px 5px 0 rgb(255,255,255)",
          color : "Black" ,
         
         }}
      
        animate={{
          scale: [1, 2, 2, 1, 1],
          rotate: [0, 0, 360, 360, 0],
          borderRadius: ["20%", "20%", "50%", "50%", "20%"],
        }}> 

        <img src={logo} alt="" /> </motion.span>
        
        
       
        <Blink color='white' text='Get In touch with me !' fontSize='2em'>
        </Blink> 

            
          

        <app />
        <motion.buttona 
        whileHover={{ scale:1.05 , 
          textShadow : "0 10px 5px 0 rgb(255,215,255)",
          color : "Black" ,}}>
        <strong>Ingnieur dtat en lectromcanique | Conception Fonctionnelle | Contrle et Commande</strong> 
        </motion.buttona>
        <br /> <br /><br />
       
        <p>
         Ag de 23 ans, je suis un ingnieur d'tat de l'Ecole Nationale Supriere des Mines de Rabat,
         Filire Electromcanique promotion Juillet 2020. Motiv, srieux apte  fournir des prestations de
         haut niveau. Comptent pour examiner le budget d'un projet, effectuer des essais de systmes et de
         composants et valuer les donnes d'essais pour tirer des conclusions. Capable de crer des rapports
         d'tat et de documentation prsentant les processus.
         </p>
   </header>
)

export default Header
